.header {
	position: relative;
	height: 912px;
	width: 100vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-position: center;
	align-items: center;
	margin-top: -100px;
	/* background-image: url("../../img/Fearless.jpg");*/
	background-size: cover;
	background-color: black;
	align-items: center;
	padding: 50px;
}
.css-typing {
	background-color: black;
	height: 751px;
	width: 100%;
	background-size: contain;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-self: baseline;
}
.css-typing p {
	border-right: .15em solid orange;
	font-family: 'Roboto', sans-serif;
	font-size: 25px;
	white-space: nowrap; /* The white-space property specifies how white-space inside an element is handled */
	overflow: hidden;
	color: white;
}
.css-typing p:nth-child(1) {
	width: 7.3em;
	-webkit-animation: type 2s steps(40, end);
	animation: type 2s steps(40, end), blink .5s step-end infinite alternate;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
}

.css-typing p:nth-child(2) {
	width: 11.5em;
	opacity: 0;
	-webkit-animation: type2 2s steps(40, end);
	animation: type2 2s steps(40, end), blink .5s step-end infinite alternate;
	-webkit-animation-delay: 2s;
	animation-delay: 2s;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
}

.css-typing p:nth-child(3) {
	width: 12.3em;
	opacity: 0;
	/* -webkit-animation: type3 5s steps(20, end), blink .5s step-end infinite alternate; */
	animation: type2 2s steps(20, end), blink .5s step-end infinite alternate;
	-webkit-animation-delay: 4s;
	animation-delay: 4s;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
	color: white;
	z-index: 200;
}

@keyframes type {
	0% {
		width: 0;
	}
	99.9% {
		border-right: .15em solid orange;
	}
	100% {
		border: none;
	}
}

@keyframes type2 {
	0% {
		width: 0;
	}
	1% {
		opacity: 1;
	}
	99.9% {
		border-right: .15em solid orange;
	}
	100% {
		opacity: 1;
		border: none;
	}
}

@keyframes type3 {
	0% {
		width: 0;
	}
	1% {
		opacity: 1;
	}
	100% {
		opacity: 1;
	}
}

@keyframes blink {
	50% {
		border-color: transparent;
	}
}
@-webkit-keyframes blink {
	50% {
		border-color: tranparent;
	}
}
button {
	border: none;
	background: black;
	outline: none;
}
